import React from 'react'
import { Router, Route, Switch } from 'react-router-dom'
import { history } from './history'
import Loadable from 'react-loadable'
import Header from './components/header';
import Footer from './components/footer';

const Loading = () => <div className="tech-global-web h-100"></div>

const AboutScreen = Loadable({
    loader: () => import('./views/about'),
    loading: Loading
})

const HomeScreen = Loadable({
    loader: () => import('./views/home'),
    loading: Loading
})

const ServiceScreen = Loadable({
    loader: () => import('./views/services'),
    loading: Loading
})

const ContactScreen = Loadable({
    loader: () => import('./views/contact'),
    loading: Loading
})

const CareerScreen = Loadable({
    loader: () => import('./views/career'),
    loading: Loading
})


class Routes extends React.Component {

    render() {
        return (
            <Router history={history}>
                <Header history={history} />
                <Switch>
                    <Route path='/about' component={AboutScreen} />
                    <Route path='/home' component={HomeScreen} />
                    <Route path='/services' component={ServiceScreen} />
                    <Route path='/contact' component={ContactScreen} />
                    <Route path='/career' component={CareerScreen} />
                    {/* <Route path='/class' component={ClassComponent} />
                    <Route path='/hooks' component={HooksComponent} /> */}
                    <Route path='*' component={HomeScreen} />
                </Switch>
                <Footer history={history} />
            </Router>
        )
    }

}

export default Routes;
